.st-tree {
  padding: 0;
  margin: 0;
}
.st-tree li {
  list-style-type: none;
}
.st-tree .st-tree__icon {
  display: inline-block;
  width: 1.5em;
  line-height: 1.5em;
  margin: 0.1em;
  background-color: #eee;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
}
.st-tree li.st-tree__node--closed > ul {
  display: none;
}
.st-tree li:not(.st-tree__node--closed) > .st-tree__icon:before {
  content: "-";
}
.st-tree li.st-tree__node--closed > .st-tree__icon:before {
  content: "+";
}
.st-tree li.st-tree__node--leaf > .st-tree__icon {
  visibility: hidden;
}